<template>
  <v-app>
    <!-- <Nav /> -->
    <router-view></router-view>
    <nav>
      <v-app-bar app id="navbar" flat class="mt-3" prominent>
        <v-tabs height="65px" background-color="transparent">
          <div class="d-flex align-center pt-3 ml-md-8">
            <a href="https://quadusk.com/" target="_blank">
              <v-img
                class="shrink mr-2"
                contain
                src="@/assets/QUADUSK_Logo.png"
                transition="scale-transition"
                width="40"
              />
            </a>
            <v-card
              flat
              id="nav_text"
              style="
                font-family: 'Noto Serif', serif;
                font-size: 23px;
                background-color: transparent;
                color: white;
              "
            >
              <a
                href="https://quadusk.com/"
                target="_blank"
                style="text-decoration: none"
                class="white--text"
                >QUADUSK</a
              >
            </v-card>
          </div>
          <v-spacer></v-spacer>
          <v-tabs-slider color="#ffffff00" style="opacity: .4"></v-tabs-slider>

          <a
            v-for="item in items"
            :key="item.navText"
            class="d-flex"
            style="text-decoration: none"
            :href="item.link"
          >
            <v-tab
              id="nav_text"
              class="text-uppercase white--text hidden-sm-and-down"
            >
              {{ item.navText }}
            </v-tab>
          </a>
        </v-tabs>
        <v-app-bar-nav-icon
          class="white--text hidden-md-and-up"
          @click="drawer = !drawer"
        ></v-app-bar-nav-icon>

        <v-spacer></v-spacer>
      </v-app-bar>

      <v-navigation-drawer
        v-model="drawer"
        app
        class="white d-md-none d-xl-none d-lg-none d-sm-flex"
        right
      >
        <div class="mt-10">
          <a
            v-for="item in items"
            :key="item.navText"
            class="text-uppercase text-center"
            :href="item.link"
            style="text-decoration: none"
          >
            <p>{{ item.navText }}</p>
          </a>
        </div>
      </v-navigation-drawer>
    </nav>

    <div>
      <div class="sections-menu" style="z-index: 10000">
        <span
          class="menu-point"
          v-bind:class="{ active: activeSection == index }"
          v-on:click="scrollToSection(index)"
          v-for="(offset, index) in offsets"
          v-bind:key="index"
        >
        </span>
      </div>
      <section class="fullpage" id="/">
        <quadusk style="width: 100%" />
      </section>
      <section class="fullpage" id="service">
        <service style="width: 100%" />
      </section>
      <section class="fullpage" id="partner">
        <partner style="width: 100%" />
      </section>
      <section class="fullpage" id="contact">
        <contact style="width: 100%" />
      </section>
    </div>
    <div id="app">
      <div
        :class="[
          'g-cursor',
          { 'g-cursor_hover': hover },
          { 'g-cursor_hide': hideCursor },
        ]"
      >
        <div :style="cursorCircle" class="g-cursor__circle"></div>
        <div class="g-cursor__point" ref="point" :style="cursorPoint"></div>
      </div>
    </div>
  </v-app>
</template>

<script>
import Quadusk from "./components/quadusk";
import Service from "./components/Service";
import Partner from "./components/Partner";
import contact from "./components/Contact";
// import Nav from "./components/navbar";
export default {
  name: "App",

  components: {
    // Nav,
    Quadusk,
    Service,
    Partner,
    contact,
  },
  mounted() {
    document.getElementsByTagName("section")[0].scrollIntoView({
      behavior: "smooth",
    });
    let sections = document.getElementsByTagName("section");
    let length = sections.length;
    //   console.log("cal off")
    //   console.log(sections)
    for (let i = 0; i < length; i++) {
      let sectionOffset = sections[i];
      //console.log(1)
      this.offsets.push(sectionOffset);
    }
    //console.log(this.offsets)

    document.addEventListener("mousemove", this.moveCursor);
    document.addEventListener("mouseleave", () => {
      this.hideCursor = true;
    });
    document.addEventListener("mouseenter", () => {
      this.hideCursor = false;
    });
  },
  methods: {
    moveCursor(e) {
      this.xChild = e.clientX;
      this.yChild = e.clientY;
      setTimeout(() => {
        this.xParent = e.clientX - 15;
        this.yParent = e.clientY - 15;
      }, 100);
    },
    handleMouseWheel: function (e) {
      if (e.wheelDelta < 30 && !this.inMove) {
        this.moveUp();
        // console.log("mouse wheel");
      } else if (e.wheelDelta > 30 && !this.inMove) {
        this.moveDown();
      }

      e.preventDefault();
      return false;
    },
    handleMouseWheelDOM: function (e) {
      if (e.detail >= 0 && !this.inMove) {
        this.moveUp();
      } else if (e.detail < 0 && !this.inMove) {
        this.moveDown();
      }

      return false;
    },
    keyUpDown: function (e) {
      if (this.keyUp[e.keyCode]) {
        this.moveDown();
        e.preventDefault();
      } else if (this.keyDown[e.keyCode]) {
        this.moveUp();
        e.preventDefault();
      }
    },
    moveUp() {
      this.inMove = true;
      this.activeSection = this.activeSection + 1;
      //   console.log(this.inMove)
      //   console.log(this.activeSection + "activesection");
      //   console.log("moveup")
      //   console.log(this.offsets.length + "off")
      if (this.activeSection > this.offsets.length - 1) this.activeSection = 0;

      this.scrollToSection(this.activeSection, true);
    },
    moveDown() {
      this.inMove = true;
      this.activeSection--;

      if (this.activeSection < 0) this.activeSection = this.offsets.length - 1;

      this.scrollToSection(this.activeSection, true);
    },

    scrollToSection(id, force = false) {
      if (this.inMove && !force) return false;

      this.activeSection = id;
      this.inMove = true;

      //console.log(id)
      document.getElementsByTagName("section")[id].scrollIntoView({
        behavior: "smooth",
      });
      setTimeout(() => {
        this.inMove = false;
      }, 400);
    },
    touchStart(e) {
      e.preventDefault();

      this.touchStartY = e.touches[0].clientY;
    },
    touchMove(e) {
      if (this.inMove) return false;
      e.preventDefault();

      const currentY = e.touches[0].clientY;

      if (this.touchStartY < currentY) {
        this.moveDown();
      } else {
        this.moveUp();
      }

      this.touchStartY = 0;
      return false;
    },
  },
  created() {
    window.addEventListener("DOMMouseScroll", this.handleMouseWheelDOM); // Mozilla Firefoxe
    window.addEventListener("mousewheel", this.handleMouseWheel, {
      passive: false,
    }); // Other browsers
    //window.addEventListener('keyup', this.keyUp,{passive:false})
    window.addEventListener("keydown", this.keyUpDown);

    window.addEventListener("touchstart", this.touchStart, { passive: false }); // mobile devices
    window.addEventListener("touchmove", this.touchMove, { passive: false }); // mobile devices
  },
  destroyed() {
    window.removeEventListener("mousewheel", this.handleMouseWheel, {
      passive: false,
    }); // Other browsers
    window.removeEventListener("DOMMouseScroll", this.handleMouseWheelDOM); // Mozilla Firefox
    window.removeEventListener("keydown", this.keyUpDown);
    //window.removeEventListener('keyup', this.keyUp, {passive: false})

    window.removeEventListener("touchstart", this.touchStart); // mobile devices
    window.removeEventListener("touchmove", this.touchMove); // mobile devices
  },
  computed: {
    cursorCircle() {
      return `transform: translateX(${this.xParent}px) translateY(${this.yParent}px) translateZ(0) translate3d(0, 0, 0);`;
    },
    cursorPoint() {
      return `transform: translateX(${this.xChild - 3}px) translateY(${
        this.yChild - 3
      }px) translateZ(0) translate3d(0, 0, 0);`;
    },
  },

  data: () => ({
    xChild: 0,
    yChild: 0,
    xParent: 0,
    yParent: 0,
    hover: false,
    hideCursor: true,
    inMove: false,
    activeSection: 0,
    offsets: [],
    touchStartY: 0,
    keyUp: { 38: 1, 33: 1 },
    keyDown: { 40: 1, 34: 1 },
    drawer: false,
    tab: null,
    items: [
      {
        id: "4",
        link: "#/",
        navText: "Home",
      },
      {
        id: "5",
        link: "#service",
        navText: "Service",
      },
      {
        id: "6",
        link: "#partner",
        navText: "Partner",
      },
      {
        id: "7",
        link: "#contact",
        navText: "CONTACT",
      },
    ],
  }),
};
</script>
<style lang="scss" scoped>
.g-cursor {
  &_hide {
    opacity: 0;
    width: 60px;
    height: 60px;
    transition: width 0.6s ease, height 0.6s ease, opacity 0.6s ease;
  }

  &__circle {
    pointer-events: none;
    user-select: none;
    top: 0;
    left: 0;
    position: fixed;
    width: 30px;
    height: 30px;
    border: 2px solid #fff;
    border-radius: 100%;
    z-index: 5555;
    backface-visibility: hidden;
    transition: opacity 0.6s ease;
  }

  &__point {
    top: 0;
    left: 0;
    position: fixed;
    width: 10px;
    height: 10px;
    pointer-events: none;
    user-select: none;
    border-radius: 100%;
    background: #fff;
    z-index: 55555555;
    backface-visibility: hidden;
    will-change: transform;
  }

  &_hover {
    .g-cursor__circle {
      opacity: 0;
      width: 60px;
      height: 60px;
      transition: width 0.6s ease, height 0.6s ease, opacity 0.6s ease;
    }
  }
}
</style>
