<template>
  <div>
    <v-img
      height="100vh"
      src="section4.jpg"
      gradient="to top right, rgba(0,0,0,.33), rgba(25,32,72,.7)"
      scrolling="no"
      aspect-ratio="1"
    >
      <template v-slot:placeholder>
        <v-row class="fill-height ma-0" align="center" justify="center">
          <v-progress-circular
            indeterminate
            color="grey lighten-5"
          ></v-progress-circular>
        </v-row>
      </template>
      <v-container fill-height fluid style="z-index: 100000" class="mt-10">
        <v-row align="center" justify="center">
          <v-col align="center" justify="center">
            <p
              class="text-uppercase white--text"
              style="letter-spacing: 0.775em"
            >
              contact
            </p>
            <p class="text-uppercase white--text header-text">
              LEAVE A MESSAGE HERE
            </p>
            <v-container>
              <v-form ref="form" lazy-validation>
                <v-row justify="center">
                  <v-col cols="12" sm="6" md="6" data-aos="fade-right">
                    <v-text-field
                      color="white"
                      v-model="firstname"
                      :rules="nameRules"
                      label="First name"
                      required
                      dark
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="6" data-aos="fade-right">
                    <v-text-field
                      color="primary"
                      v-model="lastname"
                      :rules="nameRules"
                      label="Last name"
                      required
                      dark
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row justify="center" class="each_part pt-md-6">
                  <v-col cols="12" sm="6" md="6" data-aos="fade-right">
                    <v-text-field
                      dense
                      color="primary"
                      v-model="email"
                      :rules="emailRules"
                      label="E-mail"
                      required
                      dark
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="6" data-aos="fade-left">
                    <v-text-field
                      dense
                      color="primary"
                      v-model="number"
                      counter="10"
                      :rules="numberRules"
                      label="Phone Number"
                      required
                      dark
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-form>
              <v-row justify="center" class="each_part pt-md-6" data-aos="fade-left">
                <v-col>
                  <v-textarea
                    outlined
                    counter="150"
                    v-model="query"
                    :rules="queryRules"
                    label="Type your message here"
                    dark
                  ></v-textarea>
                  <v-btn right outlined dark class="rounded-pill px-10" data-aos="fade-down"> submit </v-btn>
                </v-col>
              </v-row>
            </v-container>
          </v-col>
        </v-row>
      </v-container>
    </v-img>
  </div>
</template>

<script>
export default {
  data: () => ({
    valid: true,
    firstname: "",
    lastname: "",
    email: "",
    query: "",
    number: "",
    nameRules: [
      (v) => !!v || "Name is required",
      (v) => (v && v.length <= 10) || "Name must be less than 10 characters",
    ],
    emailRules: [
      (v) => !!v || "E-mail is required",
      (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
    ],
    numberRules: [
      (v) => !!v || "Phone number is required",
      (v) => (/^[0-9]*$/.test(v) && v.length <= 10) || "number must be valid",
    ],
    queryRules: [
      (v) => !!v || "Message is required",
      (v) => (v && v.length <= 150) || "Message must be less than 100 characters",
    ],
  }),
};
</script>

