              </p>
              <v-row align="center" justify="center" class="pt-6">
                <v-col
                  class="white--text text-uppercase font-weight-light"
                  style="
                    line-height: 5px;
                    background: rgba(255, 255, 255, 0.1);
                    margin: 0 auto;
                  "
                  cols="11"
                  md="6"
                  lg="4"
                  xl="5"
                  align="center"
                  justify="center"
                >
                  <v-row align="center">
                    <v-col cols="4" id="properties">
